/**
 * Image Collage Editor Styles
 */

.multi-block-mayhem-editor.wp-block-multi-block-mayhem-image-collage {

	.block-editor-inner-blocks {

		.block-editor-block-list__layout {
			display: grid;
			grid-template-columns: repeat(var(--mb-mayhem-image-collage-cols), minmax(0, 1fr));
			grid-gap: var(--mb-mayhem-image-collage-gap);
		}
	}
}
